body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header{
  background: #ccc;
  width:100%;
 
  top:0;
  left:0;
  right:0;
  z-index: 999;
  /* height:10px; */
}

.container{
  width:90%;
  margin:auto;
}

.logo{
  width:50%;
  /* height:200px; */
}

.nav{
  width:50%;
  position:fixed;
  top:0;
  bottom:0;
  left:0;
  background:black;
  opacity:.9;
}
.nav ul{
  display:flex;
  flex-direction: column;
  margin-top: 200px;
}
.nav ul li{
  list-style-type: none;
  padding:10px;
}
.nav ul li a{
  color:white;
  text-decoration: none;
}


.checkout{
  position:fixed;
  right:0;
  bottom:20px;
  background:green;
  color:white;
  border-radius: 20px;
  padding:10px;
  border:none;
}